const getDatabaseById = async (blogClient) => {
  try {
    const notion = blogClient.client;
    if (!notion) {
      return err({ code: 400, message: "Invalid or missing notion secret" });
    }
    const database = await notion.databases.query({
      database_id: blogClient.config.databaseId,
      filter: {
        property: "Published",
        checkbox: {
          "equals": true
        }
      }
    });
    const results = database.results;
    if (isPageObjectResponse(results) && results?.length > 0) {
      const result = results;
      return ok(result);
    } else {
      return ok([]);
    }
  } catch (error) {
    return handleNotionError(error);
  }
};
const getPageBySlug = async (blogClient, slug) => {
  try {
    const notion = blogClient.client;
    if (!notion) {
      return err({ code: 400, message: "Invalid or missing notion secret" });
    }
    const res = await notion.databases.query({
      database_id: blogClient.config.databaseId,
      filter: {
        property: "Slug",
        rich_text: {
          equals: slug
        }
      }
    });
    let pages = [];
    if (res) {
      for (const page of res.results) {
        if (!isFullPage(page)) {
          continue;
        }
        pages.push(page);
      }
      return ok(pages);
    } else {
      return err({ code: 500, message: "Some error ocurred" });
    }
  } catch (error) {
    return handleNotionError(error);
  }
};
const getBlocks = async (blogClient, blockId) => {
  const notion = blogClient.client;
  if (!notion) {
    return err({ code: 400, message: "Invalid or missing notion secret" });
  }
  const { results } = await notion.blocks.children.list({
    block_id: blockId,
    page_size: 100
  });
  let blocks = [];
  if (results && results.length > 0) {
    for (const block of results) {
      if (!isFullBlock(block)) continue;
      blocks.push(block);
    }
    return ok(blocks);
  } else if (results && results.length == 0) {
    return ok([]);
  } else {
    return err({ code: 500, message: "Unknown Error" });
  }
};
const handleNotionError = (error) => {
  if (isNotionClientError(error)) {
    switch (error.code) {
      case ClientErrorCode.RequestTimeout:
        return err({ code: 400, message: "Request could not be completed" });
      case APIErrorCode.ObjectNotFound:
        return err({ code: 500, message: "Database of page not found" });
      case APIErrorCode.Unauthorized:
        return err({ code: 401, message: "User not authorized" });
      default:
        return err({ code: 500, message: "Some error ocurred" });
    }
  } else {
    return err({ code: 400, message: error ? Object.keys(error)?.length > 0 ? "" : "" : "Some error ocurred" });
  }
};
function isPageObjectResponse(response) {
  return response?.[0]?.properties !== null && response?.[0]?.properties !== void 0;
}
const getFAQs = async (blogClent, id) => {
  const response = await getBlocks(blogClent, id);
  if (response.isOk()) {
    const faqs = response.value.map((row) => {
      if (row.type == "table_row") {
        return {
          question: row.table_row.cells?.[0]?.[0]?.plain_text,
          answer: row.table_row.cells?.[1]?.map((r) => r.plain_text)?.join("")
        };
      } else {
        return {
          question: null,
          answer: null
        };
      }
    });
    return ok(faqs);
  } else if (response.isErr()) {
    return err(response.error);
  } else {
    return err({
      code: 500,
      message: "Unknown Error"
    });
  }
};
